<template>
    <div id="view-cow">
        <h3>View a Cow</h3>
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{name}}</h4></li>
            <li class="collection-item">Cow_id#:{{cow_id}}</li>
            <li class="collection-item">Farm:{{farm}}</li>
        
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteCow" class="btn red">Delete</button>
        <div class="fixed-action-btn">
            <router-link v-bind:to="{name:'edit-cow',params:{cow_id:cow_id}}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-cow',
    data () {
        return {
            cow_id:null,
            name:null,
            farm:null
        }
    },
    beforeRouteEnter(to, from, next) {
        const refStage = db.ref('_stageEngine');
        const qryStage= refStage.orderByChild('cow_id').equalTo(to.params.cow_id)
        qryStage.once('value', snap=>{
            snap.forEach(cow =>{
                next(vm=>{
                    vm.cow_id=cow.val().cow_id,
                    vm.name=cow.val().name,
                    vm.farm=cow.val().farm
                })
            })
        })
    },
    watch: {
        '$route':'fetchData'
    },
    methods: {
        fetchData() {
            db.ref('_stageEngine').orderByChild('cow_id').equalTo(this.$route.params.cow_id)
            .once('value', snap => {
                snap.forEach(cow=>{
                    this.cow_id=cow.val().cow_id
                    this.name=cow.val().name
                    this.farm=cow.val().farm
                })
            })
        },
        deleteCow() {
            if(confirm('Are you sure?')) {
                db.ref('_stageEngine').orderByChild('cow_id').equalTo(this.$route.params.cow_id)
                .once('value', snap => {
                    snap.forEach(cow=>{
                        cow.ref.remove()    
                    })
                })
                this.$route.push('/')
            }
        }
    }
}
</script>



