import Vue from 'vue'
import Router from 'vue-router'
//import HelloWorld from '@/components/HelloWorld'
import Dashboard from '@/components/Dashboard'
import NewCow from '@/components/NewCow'
import ViewCow from '@/components/ViewCow'
import EditCow from '@/components/EditCow'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'new-cow',
      component: NewCow
    },
    {
      path: '/:cow_id',
      name: 'view-cow',
      component: ViewCow
    },
    {
      path: '/edit/:cow_id',
      name: 'edit-cow',
      component: EditCow
    }
  ]
})



