<template>
    <div id="new-cow">
        <h3>New Cow</h3>
        <div class="row">
            <form @submit.prevent="saveCow" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="cow_id" required>
                        <label>Cow Id#</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <label>Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="farm" required>
                        <label>Farm</label>
                    </div>
                </div>  
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-cow',
    data () {
        return {
            cow_id:null,
            name:null,
            farm:null
        }
    },
    methods: {
        saveCow() {
            db.ref('_stageEngine').push({
                cow_id:this.cow_id,
                name:this.name,
                farm:this.farm
            })
            .then(docRef =>{
                this.$router.push("/")
            })
            .catch(error=>console.log(error))
        }
    }
}
</script>



