<template>
    <div id="dashboard">
        <h3>Dashboard</h3>
        <ul class="collection with-header">
            <li class="collection-header"><h4>Cows</h4></li>
            <li v-for="cow in cows" v-bind:key="cow._id" class="collection-item">
                <div class="chip">{{cow.farm}}</div>{{cow.cow_id}}:{{cow.name}}
                <router-link class="secondary-content" v-bind:to="{name:'view-cow', params:{cow_id:cow.cow_id}}">
                    <i class="fa fa-eye"></i>
                </router-link>

            </li>

        </ul>
        <div class="fixed-action-btn">
            <router-link to="new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data () {
        return {
            cows: []
        }
    },
    created() {
        // db.ref('cowEngine/XI9BMwrwRsbVZdakVq7fHm6vMbw2/cattleinfo').on('value',snap=>{
        //     snap.forEach(cow => {
        //         console.log(cow)
        //         const cowdata = {

        //         }
        //     });
        // })
        const refStage = db.ref('_stageEngine');
        refStage.on('value', snap=>{
            snap.forEach(cow =>{
                const data = {
                    '_id':cow.key,
                    'cow_id':cow.val().cow_id,
                    'name':cow.val().name,
                    'farm':cow.val().farm
                }
                this.cows.push(data)
            })
        });
    }
}
</script>



