<template>
    <div id="edit-cow">
        <h3>Edit Cow</h3>
        <div class="row">
            <form @submit.prevent="updateCow" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="cow_id" required>
                        <!-- <label>Cow Id#</label> -->
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <!-- <label>Name</label> -->
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="farm" required>
                        <!-- <label>Farm</label> -->
                    </div>
                </div>  
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>        
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-cow',
    data () {
        return {
            cow_id:null,
            name:null,
            farm:null
        }
    },
    beforeRouteEnter(to, from, next) {
        const refStage = db.ref('_stageEngine');
        const qryStage= refStage.orderByChild('cow_id').equalTo(to.params.cow_id)
        qryStage.once('value', snap=>{
            snap.forEach(cow =>{
                next(vm=>{
                    vm.cow_id=cow.val().cow_id,
                    vm.name=cow.val().name,
                    vm.farm=cow.val().farm
                })
            })
        })
    },
    watch: {
        '$route':'fetchData'
    },
    methods: {
        fetchData() {
            db.ref('_stageEngine').orderByChild('cow_id').equalTo(this.$route.params.cow_id)
            .once('value', snap => {
                snap.forEach(cow=>{
                    this.cow_id=cow.val().cow_id
                    this.name=cow.val().name
                    this.farm=cow.val().farm
                })
            })
        },
        updateCow() {
            if(confirm('Are you sure?')) {
                db.ref('_stageEngine').orderByChild('cow_id').equalTo(this.$route.params.cow_id)
                .once('value', snap => {
                    snap.forEach(cow=>{
                        cow.ref.update({
                            cow_id: this.cow_id,
                            name: this.name,
                            farm: this.farm
                        })
                        .then(()=>{
                            this.$router.push({name:'view-cow',
                            params:{cow_id:this.cow_id}})
                        })
                    })
                })
                this.$route.push('/')
            }
        }
    }    
}
</script>



